export const DepositBannersList = observer(() => {
    const { t } = useTranslation(["overview"]);

    const isMobile = useMobile();
    const s = useRootStore();

    return (
        <>
            {s.transfer.pendingDeposits.length ? (
                <BannerContainer>
                    <TransactionType.TransactionTypeIcon type={TransactionTypes.funds_added} width="60px" />
                    <div>
                        {isMobile ? (
                            <>
                                {t("deposit-banner-list.pending-message-mobile")}{" "}
                                <I18nLink to="/transactions"> {t("deposit-banner-list.transaction-link")}</I18nLink>{" "}
                                {t("deposit-banner-list.pending-message-mobile-cont")}{" "}
                            </>
                        ) : (
                            <>
                                {t("deposit-banner-list.pending-message")}{" "}
                                <I18nLink to="/transactions">{t("deposit-banner-list.transaction-link")} </I18nLink>{" "}
                                {t("deposit-banner-list.pending-message-cont")}
                            </>
                        )}
                    </div>
                </BannerContainer>
            ) : null}
            {s.transfer.sourcingWines.length ? (
                <BannerContainer>
                    <TransactionType.TransactionTypeIcon type={TransactionTypes.sourcing_wine} width="60px" />
                    <div>
                        {isMobile ? (
                            <>
                                {t("deposit-banner-list.sourced-wine-message-mobile")}{" "}
                                <I18nLink to="./transactions">{t("deposit-banner-list.transaction-link")} </I18nLink>{" "}
                                {t("deposit-banner-list.pending-message-mobile-cont")}
                            </>
                        ) : (
                            <>
                                {t("deposit-banner-list.sourced-wine-message")}{" "}
                                <I18nLink to="./transactions">{t("deposit-banner-list.transaction-link")} </I18nLink>{" "}
                                {t("deposit-banner-list.sourced-wine-message-cont")}
                            </>
                        )}
                    </div>
                </BannerContainer>
            ) : null}
            {/* <BannerContainer>
                <TransactionType.TransactionTypeIcon type="foundWineForYou" width="60px" />
                <div>
                    {isMobile ? (
                        <>
                            Congratulations! We've found wine for you. Go check it out in your{" "}
                            <I18nLink to="./portfolio">Portfolio </I18nLink> tab.
                        </>
                    ) : (
                        <>
                            Congratulations! We've found wine for you. Go check it out in your{" "}
                            <I18nLink to="./portfolio">Portfolio </I18nLink> tab. Depending on your buying power, you'll
                            continue receiving updates as we purchase wines for your portfolio.
                        </>
                    )}
                </div>
            </BannerContainer> */}
        </>
    );
});

const BannerContainer = styled(TopBannerContainer)`
    padding: 30px 24px;
    background: #fff;
    border: 1px solid #eeeeee;
    box-sizing: border-box;
    box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    flex-direction: row !important;

    div {
        margin-left: 20px;
        font-family: VinovestMedium;
        font-style: normal;
        font-weight: 500;
        letter-spacing: 0.005em;
        color: #242e35;
        font-size: 14px;
        line-height: 21px;
        text-align: left;

        a {
            text-decoration: none;
            color: #a86d37;
        }
    }

    ${(p) => p.theme.media.greaterThan("769px")`
        padding: 20px 62px;

        div {
            margin-left: 23px;
            font-size: 16px;
            line-height: 26px;
        }
    `}
`;
