import _ from "lodash";

// webpack exposes a context() method
const ctxSVGFiles = require.context("./", true, /\.svg$/);

/**
 * name string
 * src *.svg.src
 */
export const HistoricalThumbnailMap = _.shuffle(
    _.map(ctxSVGFiles.keys(), (m) => ({
        // remove slash path, remove svg extension, remove hyphens, capitalize
        name: m.slice(2).replace(".svg", "").split("-").map(_.capitalize).join(" "),
        src: ctxSVGFiles(m),
    })),
);
