import React, { useMemo } from "react";
import Skeleton from "react-loading-skeleton";
import styled from "styled-components";
import { ROUTE_PATHS } from "#screens/route-paths";
import { observer } from "mobx-react-lite";
import { useRootStore, useMobile } from "#shared/hooks";
import { getData, getStatus } from "#models/FetchStatus";
import { currencyFormatter, capitalize } from "#utils/shared";
import { BaseModuleContainer } from "./styles";
import { useTranslation } from "react-i18next";
import { I18nLink } from "#localization/localizedRouter";

export const PersonalSnapshot = observer(() => {
    const { t } = useTranslation(["overview"]);
    const s = useRootStore();
    const isMobile = useMobile();

    const firstName = useMemo(() => {
        const ou = getData(s.user.oktaUserEntity);
        const c = capitalize(ou.given_name);
        return c || t("personal-snapshot.friend");
    }, [s.user.oktaUserEntity]);

    const investmentPlan = useMemo(() => {
        const p = getData(s.user.profileEntity);
        const c = capitalize(p.investingStyle);
        return c ? `${c} ${t("personal-snapshot.investing")}` : "Not yet set";
    }, [s.user.profileEntity]);

    const fetching = useMemo(() => {
        const portfs = getStatus(s.portfolio.totalsEntity);
        const proffs = getStatus(s.user.profileEntity);
        const userfs = getStatus(s.user.oktaUserEntity);
        return portfs.isPending() || proffs.isPending() || userfs.isPending();
    }, [s.portfolio.totalsEntity, s.user.profileEntity, s.user.oktaUserEntity]);

    return (
        <BaseModuleContainer>
            <SnapshotHeader className="title-text" data-testid="name-header">
                {fetching ? (
                    <Skeleton count={isMobile ? 1 : 2} />
                ) : (
                    <>
                        {t("personal-snapshot.welcome")}, {!isMobile && <br />}
                        {firstName}
                    </>
                )}
            </SnapshotHeader>
            <LineItemContainer>
                <Subheader>{t("personal-snapshot.snapshot-header")}</Subheader>
                {fetching ? (
                    <Skeleton count={3} height="40px" />
                ) : (
                    <>
                        <Row>
                            <span className="row-label">{t("personal-snapshot.wine_collection")}</span>
                            <span className="row-value" data-testid="net-returns">
                                <Link to={ROUTE_PATHS.portfolio}>
                                    {t("common:utils.shared.bottle", { count: s.portfolio.totals.bottleCount })}
                                </Link>
                            </span>
                        </Row>
                        <Row>
                            <span className="row-label">{t("personal-snapshot.account-level")}</span>
                            <span className="row-value" data-testid="account-level">
                                {s.portfolio.accountLevel}
                            </span>
                        </Row>
                        <Row>
                            <span className="row-label">{t("personal-snapshot.investment-plan")}</span>
                            <span className="row-value" data-testid="investment-plan">
                                {investmentPlan}
                            </span>
                        </Row>
                    </>
                )}
            </LineItemContainer>
        </BaseModuleContainer>
    );
});

const Link = styled(I18nLink)`
    text-decoration: none;
    color: ${(p) => p.theme.colors.burntOrange};
`;

const SnapshotHeader = styled.div`
    text-align: center;
    margin-bottom: 16px;

    ${(p) => p.theme.media.greaterThan("768px")`
        text-align: left;
    `}

    .title-text {
        ${(p) => p.theme.media.greaterThan("1024px")`
            font-size: 65px;
        `}
    }
`;

const LineItemContainer = styled.div`
    margin-top: 24px;
    ${(p) => p.theme.media.greaterThan("1441px")`
            margin-bottom: 60px;
        `}
`;

const Subheader = styled.div`
    font-family: ${(p) => p.theme.fonts.label};
    text-transform: uppercase;
    font-size: 12px;
    margin-bottom: 18px;

    text-align: center;
    ${(p) => p.theme.media.greaterThan("768px")`
        text-align: left;     
    `}
    ${(p) => p.theme.media.greaterThan("1441px")`
        font-size: 14px;  
        line-height: 18px;  
    `}
`;

const Row = styled.div`
    font-size: 14px;
    display: flex;
    border-top: 1px solid ${(p) => p.theme.colors.borderGray};
    padding: 18px 0;
    ${(p) => p.theme.media.greaterThan("1441px")`
        font-size: 16px;  
        line-height: 26px;  
        padding: 25px 0;
        padding-top: 28px;
    `}
    .row-label {
        margin-right: auto;
    }
    .row-value {
        margin-left: auto;
    }
`;
