import React, { useCallback } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import posthog from "posthog-js";
import { useConfig } from "#shared/hooks";
import { useRootStore } from "#shared/hooks/useRootStore";
import { BaseModuleContainer } from "./styles";
import WineExperts from "./WineExperts";

export const BookACall = () => {
    const { t } = useTranslation(["overview"]);
    const store = useRootStore();
    const config = useConfig();
    const onBookACall = useCallback(() => {
        posthog.capture("calendly", { component: "BookACall", progress: "launch" });
        window.Calendly.showPopupWidget(config.calendly.url);
    }, [config]);

    return (
        <>
            {store.transfer.hasMadeTransfers ? (
                <CustomModuleContainer isRow>
                    <ValuePropSection>
                        <Typography>{t("book-call.title")}</Typography>
                        <p>{t("book-call.subtitle")}</p>
                    </ValuePropSection>

                    <ImageCTASection>
                        <WineExperts />
                        <Button onClick={onBookACall}>{t("book-call.book-button")}</Button>
                    </ImageCTASection>
                </CustomModuleContainer>
            ) : null}
        </>
    );
};

const CustomModuleContainer = styled(BaseModuleContainer)`
    display: flex;
    justify-content: space-around;
    align-items: center;

    flex-direction: column;
    ${(p) => p.theme.media.greaterThan("768px")`
        flex-direction: row;
    `}
`;

const Section = styled.div`
    display: flex;
    flex-direction: column;

    width: 100%;
    text-align: center;
    ${(p) => p.theme.media.greaterThan("768px")`
        text-align: left;
        height: 100%;
        width: 45%;
    `}
`;

const ValuePropSection = styled(Section)`
    justify-content: center;

    p {
        line-height: 32px;
        margin: 24px 0 0;

        font-size: 15px;
        ${(p) => p.theme.media.greaterThan("768px")`
            font-size: 16px;
        `}
        ${(p) => p.theme.media.greaterThan("1441px")`
            font-size: 20px;
            line-height: 36px;
        `}
    }
`;

const ImageCTASection = styled(Section)`
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    margin-top: 18px;
    ${(p) => p.theme.media.greaterThan("768px")`
        margin-top: 0;
    `}
`;

const Typography = styled.span`
    font-family: ${(p) => p.theme.fonts.title};
    font-size: 32px;
    ${(p) => p.theme.media.greaterThan("768px")`
        font-size: 48px;
    `}
    ${(p) => p.theme.media.greaterThan("1441px")`
        font-size: 64px;
        line-height: 84px; 
    `}
`;

const Button = styled.button`
    font-family: VinovestMono;
    height: 50px;
    background: ${({ theme }) => theme.colors.burntOrange};
    opacity: ${(p) => (p.disabled ? 0.7 : 1)};
    color: ${({ theme }) => theme.colors.white};
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    width: 100%;
    border: none;
    cursor: pointer;
    white-space: nowrap;
    pointer-events: ${(props) => (props.disabled ? "none" : "initial")};
    transition: box-shadow 0.3s ease-in-out;
    :hover {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
    }
    :focus {
        outline: 0;
    }
    @media screen and (min-width: 768px) {
        font-size: 16px;
        margin: 0 auto;
        max-width: ${(p) => (p.fullWidth ? "initial" : "300px")};
    }
    :disabled {
        background: ${({ theme }) => theme.colors.lightGray};
    }
`;
