import React, { useMemo, useCallback } from "react";
import Skeleton from "react-loading-skeleton";
import { observer } from "mobx-react-lite";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import { useRootStore, useMobile, useCreateRoutingCallback } from "#shared/hooks";
import { MainButton } from "#shared/ui";
import { getData } from "#models/FetchStatus";
import { capitalize } from "#utils/shared";
import { TopBannerContainer } from "./styles";
import { RiskToleranceOptions } from "#utils/constants";
import { DepositEvent } from "#screens/deposit/RootDepositPage";

export const RecommendedPlanBanner = observer(() => {
    const { t } = useTranslation(["overview"]);
    const s = useRootStore();
    const isMobile = useMobile();
    const routeTo = useCreateRoutingCallback();

    const investmentPlan = useMemo(() => {
        const p = getData(s.user.profileEntity);
        const c = capitalize(p.investingStyle);
        return c ? `${RiskToleranceOptions[p.investingStyle.toUpperCase()]} ${t("recommended-banner.investing")}` : "";
    }, [s.user.profileEntity, t]);

    const onClickFundAcct = useCallback(() => {
        routeTo("/deposit/add-funds", DepositEvent.AddFunds);
    }, [routeTo]);

    return (
        <TopBannerContainer>
            <div id="plan-banner-label-container">
                {t("recommended-banner.recommend")} {!isMobile && t("recommended-banner.investment")}{" "}
                {t("recommended-banner.plan")}
                <br />
                <span role="heading" data-name="investing-plan-label" id="investing-plan-label">
                    {investmentPlan || <Skeleton />}
                </span>
            </div>
            <MainButtonWithoutMargins onClick={onClickFundAcct}>
                {t("recommended-banner.fund")}
            </MainButtonWithoutMargins>
        </TopBannerContainer>
    );
});

const MainButtonWithoutMargins = styled(MainButton)`
    margin: 0px !important;

    ${(p) => p.theme.media.greaterThan("768px")`
        margin-right: 28px !important;
        height: 60px !important;
    `}
`;
