import React, { useCallback } from "react";
import styled from "styled-components";
import { MainButton } from "#shared/ui";
import { useCreateRoutingCallback } from "#shared/hooks";
import balloonMicrophoneHero from "#assets/shared/balloon-microphone-hero.svg";
import { BaseModuleContainer } from "./styles";
import { useTranslation } from "react-i18next";

export const ReferralModule = () => {
    const { t } = useTranslation(["overview"]);
    const routeTo = useCreateRoutingCallback();
    const onClickLearnMore = useCallback(() => {
        routeTo("/invite");
    }, [routeTo]);
    return (
        <CustomModuleContainer isRow>
            <ValuePropSection>
                <Typography>
                    {t("referral-module.invite")}
                    <br />
                    {t("referral-module.earn")}
                </Typography>
                <p>{t("referral-module.earn-description")}</p>
            </ValuePropSection>

            <ImageCTASection>
                <HeroImg src={balloonMicrophoneHero} alt="balloon-microphone-hero" />
                <MainButton onClick={onClickLearnMore}>{t("referral-module.learn")}</MainButton>
            </ImageCTASection>
        </CustomModuleContainer>
    );
};

const CustomModuleContainer = styled(BaseModuleContainer)`
    display: flex;
    justify-content: space-around;
    align-items: center;

    flex-direction: column;
    ${(p) => p.theme.media.greaterThan("768px")`
        flex-direction: row;
    `}
`;

const Section = styled.div`
    display: flex;
    flex-direction: column;

    width: 100%;
    text-align: center;
    ${(p) => p.theme.media.greaterThan("768px")`
        text-align: left;
        height: 100%;
        width: 45%;

    `}
`;

const ValuePropSection = styled(Section)`
    justify-content: center;

    p {
        line-height: 32px;
        margin: 24px 0 0;

        font-size: 15px;
        ${(p) => p.theme.media.greaterThan("768px")`
            font-size: 16px;
        `}
        ${(p) => p.theme.media.greaterThan("1441px")`
            font-size: 20px;
            line-height: 36px;
        `}
    }
`;

const ImageCTASection = styled(Section)`
    align-items: center;
    justify-content: space-around;

    flex-direction: column-reverse;
    margin-top: 18px;
    ${(p) => p.theme.media.greaterThan("768px")`
        flex-direction: column;
        margin-top: 0;
    `}
`;

const HeroImg = styled.img`
    height: 200px;

    margin-top: 18px;
    ${(p) => p.theme.media.greaterThan("768px")`
        margin-top: 0;
    `}
`;
const Typography = styled.span`
    font-family: ${(p) => p.theme.fonts.title};
    font-size: 32px;
    ${(p) => p.theme.media.greaterThan("768px")`
            font-size: 48px;
        `}
    ${(p) => p.theme.media.greaterThan("1441px")`
            font-size: 64px;
            line-height: 84px; 
        `}
`;
