import React, { useState } from "react";
import Slider from "react-slick";
import Skeleton from "react-loading-skeleton";
import styled from "styled-components";
import { useMobile } from "#shared/hooks";
import leftCarat from "#assets/shared/carat-left.svg";
import rightCarat from "#assets/shared/carat-right.svg";
import { HistoricalThumbnailMap } from "./historical-thumbnails";
import { BaseModuleContainer } from "./styles";
import { useTranslation } from "react-i18next";

export const HistoricalReturnsCarousel = () => {
    const { t } = useTranslation(["overview"]);
    const isMobile = useMobile();
    return (
        <CustomModuleContainer isRow>
            <span className="subtitle-text">{t("historical-returns.returns")}</span>
            <CarouselContainer>
                <Slider
                    className="slider-view-container"
                    accessibility
                    infinite
                    autoplay
                    adaptiveHeight
                    slidesToShow={isMobile ? 1 : 3}
                    slidesToScroll={1}
                    arrows
                    prevArrow={<Arrow src={leftCarat} alt="back-arrow" />}
                    nextArrow={<Arrow src={rightCarat} alt="next-arrow" />}
                >
                    {HistoricalThumbnailMap.map((t) => (
                        <ThumbnailWithLabel {...t} key={t.name} />
                    ))}
                </Slider>
            </CarouselContainer>
        </CustomModuleContainer>
    );
};

const ThumbnailWithLabel = ({ name, src }) => {
    const [loading, setIsLoading] = useState(true);

    return (
        <div key={name} className="thumb-container">
            <img src={src} alt={`${name}-thumbnail`} className="thumb-img" onLoad={() => setIsLoading(false)} />
            <span className="thumb-label">{name}</span>

            {loading && <Skeleton style={{ height: "220px", width: "220px", position: "absolute", top: 0, left: 0 }} />}
        </div>
    );
};

const CustomModuleContainer = styled(BaseModuleContainer)`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    .carousel .slide {
        background: none;
    }
`;

// to avoid pesky DOM prop warnings from react-slick
const Arrow = styled.img``;

const CarouselContainer = styled.div`
    display: flex;
    justify-content: space-around;

    width: 100%;
    height: 100%;

    .slider-view-container {
        height: 90%;
        width: 95%;
        margin-top: 18px;
        padding: 20px;
    }

    .thumb-container {
        position: relative;
        display: flex !important;
        flex-direction: column;
        align-items: center;
    }
    .thumb-img {
        height: 200px;
    }
    .thumb-label {
        font-size: 14px;
        text-align: center;
    }
`;
